@import url("https://fonts.googleapis.com/css?family=Open+Sans:300,400,700");
@import url(./app/libs/slick/slick.css);
@import url(./app/libs/slick/slick-theme.css);
@import url(./app/libs/materialize/css/materialize.min.css);
html {
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  margin: 0; }

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary {
  display: block; }

audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden],
template {
  display: none; }

a {
  background-color: transparent; }

a:active,
a:hover {
  outline: 0; }

abbr[title] {
  border-bottom: 1px dotted; }

b,
strong {
  font-weight: bold; }

dfn {
  font-style: italic; }

h1 {
  font-size: 2em;
  margin: 0.67em 0; }

mark {
  background: #ff0;
  color: #000; }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -0.5em; }

sub {
  bottom: -0.25em; }

img {
  border: 0; }

svg:not(:root) {
  overflow: hidden; }

figure {
  margin: 1em 40px; }

hr {
  box-sizing: border-box;
  height: 0; }

pre {
  overflow: auto; }

code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em; }

button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0; }

button {
  overflow: visible; }

button,
select {
  text-transform: none; }

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer; }

button[disabled],
html input[disabled] {
  cursor: default; }

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0; }

input {
  line-height: normal; }

input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  padding: 0; }

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  height: auto; }

input[type="search"] {
  -webkit-appearance: textfield;
  box-sizing: border-box; }

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none; }

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em; }

legend {
  border: 0;
  padding: 0; }

textarea {
  overflow: auto; }

optgroup {
  font-weight: bold; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

td,
th {
  padding: 0; }

* {
  font-family: 'Open Sans', sans-serif;
  text-align: center; }

.container {
  max-width: 1000px;
  margin: 0 auto; }

.logo__img {
  width: 55px; }

.logo__text {
  padding-left: 10px; }
  .logo__text p {
    margin: 0; }

.logo__title {
  margin-top: 0;
  text-transform: uppercase;
  font-size: 20px;
  font-weight: 600; }

.logo__subtitle {
  margin-bottom: 0;
  font-size: 18px; }

.logo a {
  display: flex;
  align-items: flex-end;
  color: #000000; }

.carousel .indicators .indicator-item {
  width: 17px;
  height: 17px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  margin-right: 10px;
  margin-left: 10px; }
  .carousel .indicators .indicator-item.active {
    background: #69d2e7;
    box-shadow: 0 0 0 2px #fff; }

.header {
  background: #69d2e7;
  border-top: 5px solid #fa6900; }
  .header__item {
    position: relative; }
  .header__wrapper {
    padding-top: 25px;
    display: flex;
    justify-content: space-between;
    align-items: center; }

.menu {
  cursor: pointer; }

.greeting__wrapper {
  color: #fff;
  background: #69d2e7;
  text-transform: uppercase;
  padding: 100px 0 150px; }
  .greeting__wrapper h1 {
    font-size: 80px;
    font-weight: 600;
    margin: 0; }
  .greeting__wrapper h2 {
    font-size: 31px;
    font-weight: 600;
    margin-top: 0; }

.presentation__wrapper {
  background: #fa6900;
  position: relative; }

.presentation__descr {
  font-size: 22px;
  font-weight: 300;
  width: 75%;
  padding-top: 50px;
  margin: 0 auto;
  color: #fff; }

.presentation__disabledJS {
  position: relative;
  top: -350px;
  margin-bottom: -300px; }
  .presentation__disabledJS--disabled {
    display: none; }

.presentation .carousel {
  height: 400px; }
  .presentation .carousel-item {
    width: 500px;
    height: auto; }
    .presentation .carousel-item img {
      width: 500px;
      height: auto; }
  .presentation .carousel__left {
    transform: rotateZ(180deg) translateY(50%);
    left: 16vw;
    z-index: 50; }
  .presentation .carousel__right {
    right: 16vw;
    z-index: 50;
    transform: translateY(-50%); }

.presentation .carousel__left, .presentation .carousel__right {
  position: absolute;
  width: 30px;
  height: auto;
  top: 50%;
  cursor: pointer;
  fill: #7f7f7f; }

.personal__wrapper {
  background: #f1f1f1;
  position: relative; }

.personal__item {
  display: block;
  color: #000; }

.personal .carousel {
  height: 500px !important; }
  .personal .carousel-item .personal__img {
    width: 165px;
    padding-top: 50px; }

.personal__name {
  font-size: 26px;
  font-weight: 600;
  margin-bottom: 0; }

.personal__title {
  font-weight: 300;
  margin-top: 0; }

.personal__descr {
  width: 35%;
  margin: 0 auto; }

.personal__disabledJS {
  background: #f1f1f1;
  padding-bottom: 50px;
  position: relative;
  top: -400px;
  margin-bottom: -400px; }
  .personal__disabledJS--disabled {
    display: none; }
  .personal__disabledJS .disabled__item {
    margin-bottom: 70px; }

.carousel.carousel-slider .carousel-item .personal__title {
  font-size: 22px; }

.about {
  background: #f1f1f1;
  text-align: center; }
  .about__info {
    position: relative;
    transform: translateY(-100px);
    margin: 0 35px;
    padding: 15px 45px;
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2); }
    .about__info h3 {
      font-size: 36px;
      text-transform: uppercase;
      font-weight: 600;
      position: relative; }
      .about__info h3:after {
        content: '';
        position: absolute;
        bottom: -20px;
        left: 50%;
        transform: translateX(-50%);
        width: 40%;
        height: 2px;
        background: #d5d5d5; }
  .about__descr {
    font-size: 24px;
    font-weight: 300;
    padding-top: 20px;
    text-align: left; }
  .about__items {
    display: flex;
    justify-content: space-between;
    margin-top: -35px; }
  .about svg {
    width: 100px;
    height: auto;
    fill: #69d2e7;
    transition: .2s fill ease-in-out; }

.item {
  width: 245px; }
  .item__descr {
    font-size: 24px; }
  .item__svg {
    background: #fff;
    padding: 30px 0;
    margin: 0 40px 20px;
    border-radius: 20px;
    cursor: pointer;
    transition: .2s background ease-in-out; }
    .item__svg .path {
      stroke: #69d2e7;
      fill: transparent; }
    .item__svg:hover {
      background: #69d2e7; }
      .item__svg:hover .path {
        stroke: #fff;
        fill: #fff; }

.additional {
  color: #fff;
  background-image: radial-gradient(circle at 80% 50%, #fff 35%, #fa6900 0); }
  .additional h3, .additional p {
    text-align: left; }
  .additional__wrapper {
    width: 45%;
    padding: 100px 0; }
  .additional__title {
    font-size: 26px;
    font-weight: 600;
    margin-bottom: 0; }
  .additional__subtitle {
    font-size: 22px;
    font-weight: 300;
    margin-top: 0; }

.details {
  background: #f1f1f1; }
  .details * {
    text-align: left; }
  .details__wrapper {
    display: flex;
    justify-content: space-between; }
  .details__info {
    width: 45%;
    margin-top: 80px; }
  .details__posts {
    width: 40%;
    margin-top: 80px;
    margin-bottom: 30px; }
    .details__posts h3 {
      font-size: 26px;
      font-weight: 600;
      margin-top: 0; }

.posts__wrapper {
  margin-top: 30px; }

.post__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px; }

.post__img {
  min-width: 90px;
  height: 90px;
  margin-right: 25px;
  background: #fff;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2); }

.post__title {
  font-size: 14px;
  font-weight: 600;
  margin-top: 0;
  margin-bottom: 0; }

.post__descr {
  font-size: 14px;
  margin-top: 8px; }

.post__text {
  margin-right: -100px; }

.social {
  display: flex;
  margin-top: 30px; }
  .social img {
    display: block;
    height: 35px;
    padding: 7px; }
  .social a {
    border: 1px solid #000;
    transition: background-color .2s ease-in-out; }
    .social a:first-child {
      border-top-left-radius: 4px;
      border-bottom-left-radius: 4px; }
    .social a:last-child {
      border-top-right-radius: 4px;
      border-bottom-right-radius: 4px; }
    .social a:not(:last-child) {
      border-right: none; }
    .social a:hover {
      background: #fa6900; }

.footer {
  background-color: #69d2e7;
  font-size: 14px; }
  .footer__wrapper {
    display: flex;
    justify-content: space-between;
    padding: 25px 0; }
  .footer__rules a {
    color: #fff;
    transition: color .2s ease-in-out; }
    .footer__rules a:first-child {
      padding-right: 10px;
      position: relative; }
      .footer__rules a:first-child:after {
        content: '';
        position: absolute;
        right: 0;
        height: 60%;
        width: 1px;
        background: #fff;
        top: 50%;
        transform: translateY(-50%); }
    .footer__rules a:last-child {
      padding-left: 5px; }
    .footer__rules a:hover {
      color: #fa6900; }
  .footer__copiright {
    color: #fff; }

@media only screen and (max-width: 768px) {
  .arrow {
    display: none; } }

@media only screen and (max-width: 1024px) {
  .about__items {
    flex-direction: column;
    align-items: center; } }

@media only screen and (max-width: 450px) {
  .about__info {
    margin: 0;
    padding: 15px; }
  .about__descr {
    font-size: 18px;
    text-align: justify; }
  .about .item__descr {
    font-size: 18px;
    margin-bottom: 50px; } }

@media only screen and (max-width: 1800px) {
  .presentation .carousel__right {
    right: 17vw; }
  .presentation .carousel__left {
    left: 17vw; } }

@media only screen and (max-width: 1650px) {
  .presentation .carousel__right {
    right: 19vw; }
  .presentation .carousel__left {
    left: 19vw; } }

@media only screen and (max-width: 550px) {
  .presentation__descr {
    width: 100%;
    font-size: 18px; }
  .presentation .carousel {
    height: 260px; }
    .presentation .carousel-item img {
      width: 300px; }
    .presentation .carousel-item__right {
      right: 16vw; }
    .presentation .carousel-item__left {
      left: 16vw; }
  .presentation .presentation__disabledJS {
    top: -230px;
    margin-bottom: -210px; } }

@media only screen and (max-width: 1024px) {
  .personal__descr {
    width: 70%; } }

@media only screen and (max-width: 520px) {
  .personal .carousel-item .personal__img {
    width: 150px; }
  .personal__descr {
    width: 80%; }
  .personal .carousel {
    height: 520px !important; } }

@media only screen and (max-width: 375px) {
  .personal__disabledJS {
    top: -470px;
    margin-bottom: -470px;
    padding-bottom: 10px; } }

@media only screen and (max-width: 320px) {
  .personal .carousel {
    height: 560px !important; } }

@media only screen and (max-width: 1250px) {
  .additional {
    background-image: radial-gradient(circle at 90% 50%, #fff 35%, #fa6900 0); } }

@media only screen and (max-width: 900px) {
  .additional {
    background-image: radial-gradient(circle at 190% 50%, #fff 70%, #fa6900 0); } }

@media only screen and (max-width: 450px) {
  .additional {
    background-image: radial-gradient(circle at 400% 50%, #fff 80%, #fa6900 0); }
    .additional__wrapper {
      width: 60%; } }

@media only screen and (max-width: 1024px) {
  .details__wrapper {
    flex-direction: column; }
  .details__info {
    width: 100%; }
  .details__posts {
    width: 100%; }
  .post__text {
    margin-right: 0; }
  .post__item {
    align-items: flex-start; } }

@media only screen and (max-width: 450px) {
  .footer__wrapper {
    flex-direction: column; }
  .footer__rules {
    margin-bottom: 10px; } }
